import {
  faBookOpen,
  faFlask,
  faHammer,
  faMusic,
  faQuoteLeft,
  faSmile,
} from '@fortawesome/free-solid-svg-icons';

export enum Category {
  ABOUT = 'about',
  BOOK = 'book',
  TECH = 'tech',
  QUOTE = 'quote',
  MUSIC = 'music',
  AI = 'ai',
  LAB = 'lab',
}

export const SectionTitle = (category: string) => {
  if (category == Category.ABOUT) {
    return ', MOZTIQ';
  } else if (category == Category.MUSIC) {
    return '  ';
  } else if (category == Category.BOOK) {
    return '  ';
  } else if (category == Category.TECH) {
    return '  ';
  } else if (category == Category.QUOTE) {
    return ' ,  ';
  } else if (category == Category.AI) {
    return 'Dances with AI';
  } else if (category == Category.LAB) {
    return '';
  }
  return '';
};

export const SectionDescription = (category: string) => {
  if (category == Category.ABOUT) {
    return 'MOZTIQ  Mozart   (Pathetique)         .';
  } else if (category == Category.MUSIC) {
    return '     .          .';
  } else if (category == Category.BOOK) {
    return '         .      .';
  } else if (category == Category.TECH) {
    return '     .     .';
  } else if (category == Category.QUOTE) {
    return '   ,  ,        .     .';
  } else if (category == Category.AI) {
    return 'AI     .    AI      .';
  } else if (category == Category.LAB) {
    return '        .          .';
  }
  return '';
};

export const SectionIcon = (category: string) => {
  if (category == Category.MUSIC) {
    return faMusic;
  } else if (category == Category.BOOK) {
    return faBookOpen;
  } else if (category == Category.TECH) {
    return faHammer;
  } else if (category == Category.QUOTE) {
    return faQuoteLeft;
  } else if (category == Category.LAB) {
    return faFlask;
  }
  return faSmile;
};
